/** @jsx jsx */
import React from "react"
import Layout from "../components/layout"
import { Box, Grid, Image, Link, Text, Heading, Container, Flex, jsx } from "theme-ui"
import { injectIntl, useIntl } from "gatsby-plugin-intl"

import Logo  from "../../static/logo.svg"
import GithubActionLogo from "../../static/nightly/actions-icon-actions.svg"

const NightlyPage = () => {
    const intl = useIntl()
    return(
        <Layout subject={"Nightly version"}>
            {/* Banner */}
            <Container sx={{
                
                
            }}>
                <Grid sx={{
                    pb:[2,3,5],
                    pt:[2,3,5],
                    pl:[1,2,2],
                    pr:[1,2,2],
                    backgroundColor: "muted"
                }}>
                    <Box>
                        <Text sx={{textAlign:"center"}}>
                            <Image src={Logo} sx={{
                                width:[32,48,64],
                                height:[32,48,64]
                            }}/>
                        </Text>
                        <Heading as="h1" sx={{
                            textAlign:"center",
                            fontWeight:600
                            }}>
                            品尝新鲜出炉的Discuz Hub。
                        </Heading>
                        <Text as="p" sx={{
                            textAlign:"center",
                            fontWeight:300,
                            mt:[1,2,2]
                        }}>
                            先行体验即将上架的Android版应用。
                        </Text>
                    </Box>
                </Grid>
                {/* downloadable contents */}
                <Grid columns={[1,'1fr 3fr 1fr','1fr 4fr 1fr']}
                    sx={{
                        pt:[2,4,6],
                        pb:[2,4,6],
                    }}
                >
                    <Box>
                        <Text sx={{
                            textAlign:["center","right"]
                        }}>
                            <Image src={GithubActionLogo} sx={{
                            width:[32,48,64],
                            height:[32,48,64]
                        }} />
                        </Text>
                        
                    </Box>
                    <Box>
                        <Heading as="h2" sx={{
                            fontWeight:700
                        }}>使用Github action构建</Heading>
                        <Text as="p">与源代码保持实时更新，抢先体验我们的下一代版本，与我们一起把它打造得尽善尽美。</Text>
                    </Box>
                    <Box>
                        <Link href="https://github.com/kidozh/DiscuzHub/actions" sx={{
                            backgroundColor:"primary",
                            p:[3,3,3],
                            color:"white",
                            borderRadius:[8,8,8],
                            ":hover":{
                                color:"white",
                                backgroundColor:"accent",
                                textDecoration: "none",
                                transition: ".35s all ease-in"
                            }
                        }}>Single APK</Link>
                    </Box>
                </Grid>

            </Container>
        </Layout>
    )
}




export default NightlyPage